import React from "react"
import { encodeObj, copyToClipboard } from "../../utils"
import { Button, Space } from "antd"
import { SmileTwoTone } from "@ant-design/icons"
import CreateForm from "./CreateForm"
import css from "./styles.module.scss"

const Home = () => {
  const [url, setUrl] = React.useState("")
  return (
    <div className={css.container}>
      <CreateForm
        onValuesChange={(_, values) => {
          setUrl(window.location + encodeObj(values.games))
        }}
      >
        <Space direction="vertical" size="large">
          <Button href={url} target="_blank">
            <SmileTwoTone />
            Try
          </Button>

          <Space>
            <Button onClick={() => copyToClipboard(url)}>Copy URL</Button>
            <span>or</span>
            <Button href={`https://tinyurl.com/create.php?url=${encodeURIComponent(url)}`} target="_blank" children="Tiny URL" />
          </Space>
        </Space>
      </CreateForm>
    </div>
  )
}

export default Home
