import React from "react"
import { Form, Input, Button, Select, InputNumber, Empty, Divider } from "antd"
import { MinusCircleTwoTone, PlusOutlined } from "@ant-design/icons"
import QueueAnim from "rc-queue-anim"
import css from "./styles.module.scss"

const CreateForm = ({ children, ...props }) => {
  const [form] = Form.useForm()
  const initialValues = { games: [{ type: "redirect" }] }

  return (
    <Form {...props} form={form} labelCol={{ span: 10 }} initialValues={initialValues} className={css.container}>
      <Form.List name="games">
        {(fields, { add, remove }) => (
          <>
            <QueueAnim>
              {fields.map((field, index) => (
                <div className={css.game} key={index}>
                  {index > 0 && <MinusCircleTwoTone twoToneColor="#e63946" className={css.removeBtn} onClick={() => remove(field.name)} />}
                  <Form.Item {...field} required={false} name={[field.name, "type"]}>
                    <Select style={{ width: "100%" }} disabled={index === 0}>
                      <Select.Option value="question">Question</Select.Option>
                      <Select.Option value="pokemonTrivia">Pokemon Trivia</Select.Option>
                      <Select.Option value="simonSays">Simon Says</Select.Option>
                      <Select.Option value="buttonClicks">Button Clicks</Select.Option>
                      <Select.Option value="qrScan">QRScan</Select.Option>
                      <Select.Option value="redirect">Redirect</Select.Option>
                    </Select>
                  </Form.Item>
                  <Form.Item noStyle shouldUpdate={true}>
                    {({ getFieldValue }) => {
                      return getFieldValue(["games", field.name, "type"]) === "question" ? (
                        <>
                          <Form.Item name={[field.name, "question"]}>
                            <Input placeholder="Your question here" />
                          </Form.Item>
                          <Form.Item name={[field.name, "hint"]}>
                            <Input placeholder="Your hint here" />
                          </Form.Item>
                          <Form.Item name={[field.name, "answers"]}>
                            <Input placeholder="Comma separated answers" />
                          </Form.Item>
                        </>
                      ) : null
                    }}
                  </Form.Item>
                  <Form.Item noStyle shouldUpdate={true}>
                    {({ getFieldValue }) => {
                      return getFieldValue(["games", field.name, "type"]) === "simonSays" ? (
                        <>
                          <Form.Item label={"Rounds to win"} name={[field.name, "rounds"]}>
                            <InputNumber min={2} max={100} placeholder="6" />
                          </Form.Item>
                        </>
                      ) : null
                    }}
                  </Form.Item>
                  <Form.Item noStyle shouldUpdate={true}>
                    {({ getFieldValue }) => {
                      return getFieldValue(["games", field.name, "type"]) === "qrScan" ? (
                        <Form.Item label={"QR Match Value"} name={[field.name, "answer"]}>
                          <Input placeholder="The value to Match" />
                          <small>Create your QR with any online generator</small>
                        </Form.Item>
                      ) : null
                    }}
                  </Form.Item>
                  <Form.Item noStyle shouldUpdate={true}>
                    {({ getFieldValue }) => {
                      return getFieldValue(["games", field.name, "type"]) === "buttonClicks" ? (
                        <Form.Item label={"Clicks to win"} name={[field.name, "clicks"]}>
                          <InputNumber min={2} max={10000} placeholder="50" />
                        </Form.Item>
                      ) : null
                    }}
                  </Form.Item>
                  <Form.Item noStyle shouldUpdate={true}>
                    {({ getFieldValue }) => {
                      return getFieldValue(["games", field.name, "type"]) === "redirect" ? (
                        <>
                          <Form.Item name={[field.name, "url"]}>
                            <Input type="url" placeholder="Your URL here" />
                          </Form.Item>
                        </>
                      ) : null
                    }}
                  </Form.Item>
                </div>
              ))}
            </QueueAnim>
            {fields.length === 1 && <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />}
            <Form.Item>
              <Button type="dashed" shape="round" size="large" onClick={() => add({})} icon={<PlusOutlined />}></Button>
            </Form.Item>
          </>
        )}
      </Form.List>
      <Divider></Divider>
      <Form.Item>{children}</Form.Item>
    </Form>
  )
}

export default CreateForm
