import stringSimilarity from "string-similarity"
import { message } from "antd"

export const compareStrings = (a, b) => {
  const similarity = stringSimilarity.compareTwoStrings(b.toLowerCase(), a.toLowerCase())
  console.log(similarity)
  return similarity
}

export const shiftChars = (str, amount, min, max) => {
  return str
    .split("")
    .map((a) => {
      const code = a.charCodeAt(0)
      if (code >= min && code <= max) {
        let res = code + amount
        const diff = max - min
        if (res > max) {
          res -= diff + 1
        } else if (res < min) {
          res += diff + 1
        }
        return String.fromCharCode(res)
      }
      return a
    })
    .join("")
}

export const encodeObj = (obj) => encode(JSON.stringify(obj))
export const decodeObj = (str) => JSON.parse(decode(str))

export const encode = (str) => {
  str = shiftChars(str, 1, 97, 122)
  str = shiftChars(str, 5, 60, 90)
  str = lzw_encode(str)
  str = encodeURIComponent(str)
  return str
}

export const decode = (str) => {
  str = decodeURIComponent(str)
  str = lzw_decode(str)
  str = shiftChars(str, -5, 60, 90)
  str = shiftChars(str, -1, 97, 122)
  return str
}

export const copyToClipboard = (str) => {
  const el = document.createElement("textarea")
  el.value = str
  document.body.appendChild(el)
  el.select()
  document.execCommand("copy")
  document.body.removeChild(el)
  message.success("URL Copied")
}

// LZW-compress a string
function lzw_encode(s) {
  var dict = {}
  var data = (s + "").split("")
  var out = []
  var currChar
  var phrase = data[0]
  var code = 256
  for (let i = 1; i < data.length; i++) {
    currChar = data[i]
    if (dict[phrase + currChar] != null) {
      phrase += currChar
    } else {
      out.push(phrase.length > 1 ? dict[phrase] : phrase.charCodeAt(0))
      dict[phrase + currChar] = code
      code++
      phrase = currChar
    }
  }
  out.push(phrase.length > 1 ? dict[phrase] : phrase.charCodeAt(0))
  for (let i = 0; i < out.length; i++) {
    out[i] = String.fromCharCode(out[i])
  }
  return out.join("")
}

// Decompress an LZW-encoded string
function lzw_decode(s) {
  var dict = {}
  var data = (s + "").split("")
  var currChar = data[0]
  var oldPhrase = currChar
  var out = [currChar]
  var code = 256
  var phrase
  for (var i = 1; i < data.length; i++) {
    var currCode = data[i].charCodeAt(0)
    if (currCode < 256) {
      phrase = data[i]
    } else {
      phrase = dict[currCode] ? dict[currCode] : oldPhrase + currChar
    }
    out.push(phrase)
    currChar = phrase.charAt(0)
    dict[code] = oldPhrase + currChar
    code++
    oldPhrase = phrase
  }
  return out.join("")
}
